'use client'

import React, { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { useNotifications } from '@/contexts/NotificationContext'
import { WorkspaceService } from '@/services/database/workspaceService'
import { InviteService } from '@/services/database/inviteService'
import { Plus, Trash2, Shield, Edit2, Mail, Link as LinkIcon } from 'lucide-react'
import MemberCard from './MemberCard'
import InviteMemberModal from './InviteMemberModal'
import { RoleBadge } from '../ui/RoleBadge'

interface WorkspaceMember {
  id: string
  email: string
  full_name: string
  role: 'admin' | 'editor' | 'viewer'
  joined_at: string
}

interface PendingInvite {
  id: string
  email?: string
  role: 'admin' | 'editor' | 'viewer'
  token: string
  expires_at: string | null
  created_at: string
}

export default function MembersTab() {
  const { user, workspaceId, userRole } = useAuth()
  const { addNotification } = useNotifications()
  const [members, setMembers] = useState<WorkspaceMember[]>([])
  const [pendingInvites, setPendingInvites] = useState<PendingInvite[]>([])
  const [loading, setLoading] = useState(true)
  const [isInviteModalOpen, setIsInviteModalOpen] = useState(false)
  const isAdmin = userRole === 'admin'

  // Load members and pending invites
  useEffect(() => {
    if (!workspaceId) return

    const loadData = async () => {
      try {
        setLoading(true)

        // Load workspace members
        const workspaceMembers = await WorkspaceService.getWorkspaceMembers(workspaceId)
        setMembers(workspaceMembers as WorkspaceMember[])

        // Load pending invites (only if admin)
        if (isAdmin) {
          const invites = await InviteService.getWorkspaceInvites(workspaceId)
          setPendingInvites(invites as PendingInvite[])
        }
      } catch (error) {
        console.error('Error loading workspace data:', error)
        addNotification('error', 'Failed to load workspace members')
      } finally {
        setLoading(false)
      }
    }

    loadData()
  }, [workspaceId, isAdmin, addNotification])

  const handleRemoveMember = async (memberId: string) => {
    if (!workspaceId) return

    const confirmed = confirm('Are you sure you want to remove this member?')
    if (!confirmed) return

    try {
      const success = await WorkspaceService.removeMember(workspaceId, memberId, user!.id)
      if (success) {
        setMembers(members.filter(m => m.id !== memberId))
        addNotification('success', 'Member removed successfully')
      } else {
        addNotification('error', 'Failed to remove member')
      }
    } catch (error) {
      console.error('Error removing member:', error)
      addNotification('error', 'Error removing member')
    }
  }

  const handleRoleChange = async (memberId: string, newRole: 'admin' | 'editor' | 'viewer') => {
    if (!workspaceId) return

    try {
      const success = await WorkspaceService.changeMemberRole(workspaceId, memberId, newRole, user!.id)
      if (success) {
        setMembers(members.map(m => m.id === memberId ? { ...m, role: newRole } : m))
        addNotification('success', 'Member role updated')
      } else {
        addNotification('error', 'Failed to update member role')
      }
    } catch (error) {
      console.error('Error updating role:', error)
      addNotification('error', 'Error updating member role')
    }
  }

  const handleRevokeInvite = async (inviteId: string) => {
    if (!workspaceId) return

    const confirmed = confirm('Are you sure you want to revoke this invitation?')
    if (!confirmed) return

    try {
      const success = await InviteService.revokeInvite(inviteId, workspaceId, user!.id)
      if (success) {
        setPendingInvites(pendingInvites.filter(i => i.id !== inviteId))
        addNotification('success', 'Invitation revoked')
      } else {
        addNotification('error', 'Failed to revoke invitation')
      }
    } catch (error) {
      console.error('Error revoking invite:', error)
      addNotification('error', 'Error revoking invitation')
    }
  }

  const handleResendInvite = async (inviteId: string) => {
    if (!workspaceId) return

    try {
      const newInvite = await InviteService.resendInvite(inviteId, workspaceId, user!.id)
      if (newInvite) {
        addNotification('success', 'Invitation resent successfully')
      } else {
        addNotification('error', 'Failed to resend invitation')
      }
    } catch (error) {
      console.error('Error resending invite:', error)
      addNotification('error', 'Error resending invitation')
    }
  }

  const handleInviteSent = () => {
    setIsInviteModalOpen(false)
    // Refresh pending invites
    if (workspaceId && isAdmin) {
      InviteService.getWorkspaceInvites(workspaceId).then(invites => {
        setPendingInvites(invites as PendingInvite[])
      })
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-slate">Loading members...</div>
      </div>
    )
  }

  return (
    <div className="space-y-8">
      {/* Current Members Section */}
      <div>
        <div className="flex items-center justify-between mb-6">
          <div>
            <h2 className="text-xl font-semibold text-charcoal">Workspace Members</h2>
            <p className="text-sm text-slate mt-1">{members.length} member{members.length !== 1 ? 's' : ''}</p>
          </div>
          {isAdmin && (
            <button
              onClick={() => setIsInviteModalOpen(true)}
              className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
            >
              <Plus size={18} />
              Invite Member
            </button>
          )}
        </div>

        <div className="space-y-3">
          {members.length === 0 ? (
            <div className="text-center py-8 bg-gray-50 rounded-lg">
              <p className="text-slate">No members yet</p>
            </div>
          ) : (
            members.map(member => (
              <MemberCard
                key={member.id}
                member={member}
                isCurrentUser={member.id === user?.id}
                isAdmin={isAdmin}
                onRemove={() => handleRemoveMember(member.id)}
                onRoleChange={(role) => handleRoleChange(member.id, role)}
              />
            ))
          )}
        </div>
      </div>

      {/* Pending Invitations Section */}
      {isAdmin && pendingInvites.length > 0 && (
        <div>
          <h2 className="text-xl font-semibold text-charcoal mb-6">Pending Invitations</h2>
          <div className="space-y-3">
            {pendingInvites.map(invite => (
              <div
                key={invite.id}
                className="flex items-center justify-between p-4 bg-gray-50 rounded-lg border border-gray-200"
              >
                <div className="flex items-center gap-4 flex-1">
                  <div className="w-10 h-10 rounded-full bg-blue-100 flex items-center justify-center">
                    {invite.email ? <Mail size={20} className="text-blue-600" /> : <LinkIcon size={20} className="text-blue-600" />}
                  </div>
                  <div className="flex-1">
                    <p className="font-medium text-charcoal">
                      {invite.email || 'Shareable Link'}
                    </p>
                    <p className="text-sm text-slate">
                      {invite.email ? `Email invitation` : `Anyone with link can join`}
                    </p>
                  </div>
                  <RoleBadge role={invite.role} size="sm" />
                </div>

                <div className="flex items-center gap-2">
                  {InviteService.isInviteExpired(invite.expires_at) && (
                    <span className="text-xs px-2 py-1 bg-red-100 text-red-700 rounded">Expired</span>
                  )}
                  <button
                    onClick={() => handleResendInvite(invite.id)}
                    className="p-2 hover:bg-white rounded transition-colors"
                    title="Resend invitation"
                  >
                    <Mail size={18} className="text-blue-600" />
                  </button>
                  <button
                    onClick={() => handleRevokeInvite(invite.id)}
                    className="p-2 hover:bg-white rounded transition-colors"
                    title="Revoke invitation"
                  >
                    <Trash2 size={18} className="text-red-600" />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Invite Modal */}
      <InviteMemberModal
        isOpen={isInviteModalOpen}
        onClose={() => setIsInviteModalOpen(false)}
        onInviteSent={handleInviteSent}
      />
    </div>
  )
}
