'use client'

import React, { useState, useEffect } from 'react'
import { useAuth } from '@/contexts/AuthContext'
import { useNotifications } from '@/contexts/NotificationContext'
import { WorkspaceService } from '@/services/database/workspaceService'
import { Save, AlertCircle } from 'lucide-react'

interface Workspace {
  id: string
  name: string
  max_users: number
  settings?: Record<string, any>
  created_at: string
  updated_at: string
}

export default function WorkspaceSettingsTab() {
  const { workspaceId, userRole } = useAuth()
  const { addNotification } = useNotifications()
  const [workspace, setWorkspace] = useState<Workspace | null>(null)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [formData, setFormData] = useState({
    name: '',
    max_users: 10
  })
  const isAdmin = userRole === 'admin'

  // Load workspace data
  useEffect(() => {
    if (!workspaceId) return

    const loadWorkspace = async () => {
      try {
        setLoading(true)
        const data = await WorkspaceService.getWorkspace(workspaceId)
        if (data) {
          setWorkspace(data as Workspace)
          setFormData({
            name: (data as any).name || '',
            max_users: (data as any).max_users || 10
          })
        }
      } catch (error) {
        console.error('Error loading workspace:', error)
        addNotification('error', 'Failed to load workspace settings')
      } finally {
        setLoading(false)
      }
    }

    loadWorkspace()
  }, [workspaceId, addNotification])

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: name === 'max_users' ? parseInt(value, 10) : value
    }))
  }

  const handleSave = async () => {
    if (!workspaceId) return

    // Validation
    if (!formData.name.trim()) {
      addNotification('error', 'Workspace name cannot be empty')
      return
    }

    if (formData.max_users < 1) {
      addNotification('error', 'Maximum users must be at least 1')
      return
    }

    try {
      setSaving(true)
      const success = await WorkspaceService.updateWorkspace(workspaceId, formData)
      if (success) {
        setWorkspace(success as Workspace)
        addNotification('success', 'Workspace settings updated successfully')
      } else {
        addNotification('error', 'Failed to update workspace settings')
      }
    } catch (error) {
      console.error('Error saving workspace:', error)
      addNotification('error', 'Error updating workspace settings')
    } finally {
      setSaving(false)
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="text-slate">Loading workspace settings...</div>
      </div>
    )
  }

  if (!isAdmin) {
    return (
      <div className="p-6 bg-yellow-50 border border-yellow-200 rounded-lg flex items-gap-3">
        <AlertCircle className="text-yellow-600 flex-shrink-0" size={20} />
        <div>
          <h3 className="font-semibold text-yellow-900">Access Denied</h3>
          <p className="text-sm text-yellow-800">Only workspace admins can modify workspace settings.</p>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-2xl space-y-8">
      {/* Workspace Name */}
      <div>
        <label htmlFor="name" className="block text-sm font-medium text-charcoal mb-2">
          Workspace Name
        </label>
        <input
          id="name"
          name="name"
          type="text"
          value={formData.name}
          onChange={handleInputChange}
          placeholder="My Workspace"
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <p className="text-xs text-slate mt-1">The name of your workspace</p>
      </div>

      {/* Max Users */}
      <div>
        <label htmlFor="max_users" className="block text-sm font-medium text-charcoal mb-2">
          Maximum Members
        </label>
        <input
          id="max_users"
          name="max_users"
          type="number"
          min="1"
          max="1000"
          value={formData.max_users}
          onChange={handleInputChange}
          className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <p className="text-xs text-slate mt-1">
          Maximum number of members allowed in this workspace
        </p>
      </div>

      {/* Current Stats */}
      {workspace && (
        <div className="p-4 bg-blue-50 rounded-lg border border-blue-200">
          <h3 className="font-semibold text-blue-900 mb-3">Workspace Information</h3>
          <div className="grid grid-cols-2 gap-4 text-sm">
            <div>
              <p className="text-blue-700 font-medium">Created</p>
              <p className="text-blue-600">
                {new Date((workspace as any).created_at).toLocaleDateString()}
              </p>
            </div>
            <div>
              <p className="text-blue-700 font-medium">Last Updated</p>
              <p className="text-blue-600">
                {new Date((workspace as any).updated_at).toLocaleDateString()}
              </p>
            </div>
          </div>
        </div>
      )}

      {/* Save Button */}
      <button
        onClick={handleSave}
        disabled={saving || !isAdmin}
        className="flex items-center gap-2 px-6 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
      >
        <Save size={18} />
        {saving ? 'Saving...' : 'Save Changes'}
      </button>
    </div>
  )
}
